import styled, { css } from 'styled-components'
import { hemelblauw } from '../colors'

export const BarBackground = styled.div`background: ${hemelblauw.darkest};`

export const titleStyle = css`
	color: inherit;
	padding: 0.9rem 1rem;
	text-align: center;
	font-size: 1.2rem;
	line-height: 1.2;
`



